import templateUrl from './home.jade';
export default {
  name: 'home',
  url: '/home',
  templateUrl,
  controller: /*@ngInject*/function(meta, $templateCache, markdown){
    this.meta = meta;
    $templateCache.put('body.html',markdown.render(meta.body));
  },
  controllerAs: 'home',
  resolve: {
    meta: /*@ngInject*/function(api){
      return api.getMetaBy_id({_id: 'home'});
    }
  }
};



